'use client';

import { useState } from 'react';
import { ethers } from 'ethers';
import { getContract } from '@/lib/contract';
import { supabase } from '@/lib/supabaseClient';


export default function InsertCertificate() {
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [formData, setFormData] = useState({
    nomeStudente: '',
    corso: '',
    data: '',
  });
  const [status, setStatus] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handlePdfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPdfFile(e.target.files[0]);
  };

  const calculateHash = async (file: File): Promise<string> => {
    const arrayBuffer = await file.arrayBuffer();
    const hashBuffer = await crypto.subtle.digest('SHA-256', arrayBuffer);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
    return hashHex;
    };


  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!pdfFile) return setStatus('Seleziona un PDF');

    setLoading(true);
    setStatus(null);

    try {
      // 1. Calcolo hash del PDF
      const hash = await calculateHash(pdfFile);

      // 2. Interazione con Smart Contract (usa provider e signer)
      if (typeof window !== "undefined" && window.ethereum) {
        const provider = new ethers.BrowserProvider(window.ethereum);
        const signer = await provider.getSigner();
        const contract = getContract(signer); // Funzione che crea l'istanza del contratto
        const tx = await contract.storeCertificateHash(hash);
      await tx.wait(); // Attendi conferma
      } else {
        console.error("MetaMask non è disponibile");
       }

      // 3. Upload PDF (opzionale) + salvataggio dati su Supabase
      const { data: uploaded, error: uploadErr } = await supabase.storage
        .from('certificati')
        .upload(`cert-${Date.now()}.pdf`, pdfFile);

      if (uploadErr) throw uploadErr;

      // 4. Inserimento metadati nel DB
      const { error: insertErr } = await supabase
        .from('certificati')
        .insert({
          hash,
          nome_studente: formData.nomeStudente,
          corso: formData.corso,
          data: formData.data,
          file_url: uploaded?.path || '',
        });

      if (insertErr) throw insertErr;

      setStatus('✅ Certificato registrato con successo!');
      setFormData({ nomeStudente: '', corso: '', data: '' });
      setPdfFile(null);
    } catch (err: any) {
      setStatus(`❌ Errore: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow-md max-w-2xl mx-auto">
      <h2 className="text-2xl font-bold mb-4 text-indigo-700">Inserisci nuovo certificato</h2>

      <form onSubmit={handleSubmit} className="flex flex-col gap-4">
        <input
          type="text"
          name="nomeStudente"
          value={formData.nomeStudente}
          onChange={handleChange}
          placeholder="Nome Studente"
          className="border rounded p-2"
          required
        />

        <input
          type="text"
          name="corso"
          value={formData.corso}
          onChange={handleChange}
          placeholder="Corso di Laurea"
          className="border rounded p-2"
          required
        />

        <input
          type="date"
          name="data"
          value={formData.data}
          onChange={handleChange}
          className="border rounded p-2"
          required
        />

        <input
          type="file"
          accept="application/pdf"
          onChange={handlePdfChange}
          className="border rounded p-2"
          required
        />

        {status && (
          <div className={`text-sm ${status.startsWith('✅') ? 'text-green-600' : 'text-red-600'}`}>
            {status}
          </div>
        )}

        <button
          type="submit"
          disabled={loading}
          className="bg-indigo-700 text-white py-2 px-4 rounded hover:bg-indigo-800"
        >
          {loading ? 'Registrazione in corso...' : 'Registra Certificato'}
        </button>
      </form>
    </div>
  );
}
