'use server';

import { supabase } from '@/lib/supabaseClient';

// Recupera certificati dell'ente autenticato
export async function getCertificatesByEntity() {
  const {
    data: { user },
    error: authError,
  } = await supabase.auth.getUser();

  if (authError || !user) {
    throw new Error('Utente non autenticato');
  }

  const { data, error } = await supabase
    .from('certificati')
    .select(`
      id,
      title,
      description,
      issued_date,
      blockchain_tx,
      student:student_id ( nome, cognome )
    `)
    .eq('entity_id', user.id)
    .order('issued_date', { ascending: false });

  if (error) {
    throw new Error(error.message);
  }

  // Mappiamo nome completo dello studente
    const certificates = data.map((cert) => {
    const student = Array.isArray(cert.student) ? cert.student[0] : cert.student;

    return {
        id: cert.id,
        title: cert.title,
        description: cert.description,
        issued_date: cert.issued_date,
        blockchain_tx: cert.blockchain_tx,
        student_name: student ? `${student.nome} ${student.cognome}` : 'N/A',
    };
    });

  return certificates;
}

// Elimina certificato per ID
export async function deleteCertificate(id: string) {
  const {
    data: { user },
    error: authError,
  } = await supabase.auth.getUser();

  if (authError || !user) {
    throw new Error('Utente non autenticato');
  }

  const { error } = await supabase
    .from('certificati')
    .delete()
    .eq('id', id)
    .eq('entity_id', user.id); // Sicurezza: può cancellare solo i propri

  if (error) {
    throw new Error(error.message);
  }
}
